import { Component, HostBinding, Input, OnInit } from '@angular/core';
import classNames from 'classnames';

@Component({
  selector: 'c-widget-progress-icon',
  templateUrl: './widget-progress-icon.component.html',
  styleUrls: ['./widget-progress-icon.component.scss']
})
export class WidgetProgressIconComponent implements OnInit {

  @Input() color: string;
  @Input() header: string;
  @Input() text: string;
  @Input() inverse: boolean;
  @Input() value = 25;

  @HostBinding('class')
  get getClasses() {
    return this.hostClasses;
  }

  get hostClasses() {
    const classes = classNames(
      {
        card : true,
        'text-white': this.inverse,
        [`bg-${this.color}`]: this.inverse && !!this.color
      },
    );
    return classes;
  }

  constructor() { }

  ngOnInit(): void {
  }

}
