import { Component, HostBinding, OnInit } from '@angular/core';

@Component({
  selector: 'c-input-group-text',
  templateUrl: './input-group-text.component.html',
  styleUrls: ['./input-group-text.component.scss'],
})
export class InputGroupTextComponent implements OnInit {
  @HostBinding('class')
  get getClasses() {
    return this.hostClasses;
  }

  get hostClasses() {
    const classes = {
      'input-group-text': true,
    };
    return classes;
  }

  constructor() {}

  ngOnInit(): void {}
}
