import {
  AfterContentChecked,
  AfterContentInit, AfterViewChecked, AfterViewInit,
  Component, ContentChild,
  ContentChildren, ElementRef,
  EventEmitter, HostBinding,
  Input, OnChanges,
  OnDestroy,
  OnInit,
  Output,
  QueryList, Renderer2, SimpleChanges,
} from '@angular/core';
import {Subscription} from 'rxjs';

import {TabsetService} from './tabset.service';
import {TabComponent} from './tab/tab.component';
import {TabPaneComponent} from './tab-pane/tab-pane.component';
import {TabListComponent} from './tab-list/tab-list.component';

@Component({
  selector: 'c-tabset',
  templateUrl: './tabset.component.html',
  styleUrls: ['./tabset.component.scss'],
  providers: [TabsetService]
})
export class TabsetComponent implements AfterContentChecked, AfterContentInit, OnChanges, OnDestroy, OnInit {

  private _activeTab = 0;
  private _selectedTab: TabComponent;
  private _tabsArray = [];
  private activeTabSubscription: Subscription;

  @Input() fill: boolean;
  @Input() justified: boolean;
  @Input() variant: 'tabs' | 'pills' = 'tabs';
  @Input() fade = true;
  @Input() vertical: boolean;

  @Input()
  set activeTab(index: number) {
    this._activeTab = index >= 0 ? index : this._activeTab;
  }
  get activeTab() {
    return this._activeTab;
  }
  @HostBinding('class.nav-tabs-boxed')
  @Input() boxed: boolean | 'left' | 'right' | 'top-right' = false;

  @Output() activeTabChange: EventEmitter<number> = new EventEmitter();

  // @ContentChildren(TabComponent, { descendants: true, read: ElementRef }) tabs !: QueryList<TabComponent>;
  @ContentChild(TabListComponent, { static: true }) private tabList !: TabListComponent;
  @ContentChildren(TabComponent, { descendants: true }) private tabs !: QueryList<TabComponent>;
  @ContentChildren(TabPaneComponent, { descendants: true }) private panes !: QueryList<TabPaneComponent>;

  private host;

  constructor(
    private hostElement: ElementRef,
    private renderer: Renderer2,
    private tabsetService: TabsetService
  ) {
    this.host = this.hostElement.nativeElement;
  }

  ngOnInit() {
    if (typeof this.boxed === 'string') {
      this.renderer.addClass(this.host, `nav-tabs-boxed-${this.boxed}`);
    }
    this.activeTabSubscribe();
  }

  ngOnDestroy() {
    this.activeTabUnsubscribe();
  }

  ngAfterContentInit() {
    // this.tabList = Object.assign(this.tabList, this.variant, this.vertical, this.fill, this.justified);
    this.tabList.variant = this.variant;
    this.tabList.vertical = this.vertical;
    this.tabList.fill = this.fill;
    this.tabList.justified = this.justified;
    this._tabsArray = this.tabs.toArray();
    if (this.tabs.length) {
      this._selectedTab = this.tabs.length - 1 < this.activeTab ? this.tabs.first : this._tabsArray[this.activeTab];
    }
    this.tabs.forEach((item, index) => {
      item.indexKey = index;
    });
    this.panes.forEach((item, index) => {
      item.indexKey = index;
    });
    this.tabsetService.setActiveTab(this._selectedTab);
    this.setActiveTab(this._selectedTab);
  }

  ngAfterContentChecked() {

  }


  ngOnChanges(changes: SimpleChanges): void {
    // console.log('tabset ngOnChanges', changes, this.activeTab);
    // this.activeTab = changes.activeTab.currentValue;
  }

  setActiveTab(tab: TabComponent = this._selectedTab) {
    this.activeTab = tab.indexKey;
    // console.log('setActiveTab', this.activeTab, tab.indexKey)
    if (!tab.disabled) {
      tab.active = true;
      this.tabsetService.setActiveTab(tab);
      this.activeTabChange.emit(this.activeTab);
    }
  }

  private activeTabSubscribe() {
    this.activeTabSubscription = this.tabsetService.activeTab$.subscribe((activeTab) => {
      // console.log('activeTabSubscription', activeTab);
      // this.setActiveTab(activeTab);
    });
  }
  private activeTabUnsubscribe() {
    this.activeTabSubscription.unsubscribe();
  }
}
