import {
  AfterContentChecked,
  Component,
  ComponentFactoryResolver, ComponentRef, ContentChildren, ElementRef,
  HostBinding,
  Input, OnDestroy,
  OnInit, QueryList, Renderer2,
  ViewChild,
  ViewContainerRef
} from '@angular/core';
import classNames from 'classnames';
import { Subscription } from 'rxjs';

import { ToasterService } from './toaster.service';
import { ToasterHostDirective } from './toaster-host.directive';
import { ToastComponent } from '../toast/toast.component';

export enum ToasterPosition {
  Static = 'static',
  TopCenter = 'top-center',
  TopFull = 'top-full',
  TopLeft = 'top-left',
  TopRight = 'top-right',
  BottomCenter = 'bottom-center',
  BottomFull = 'bottom-full',
  BottomLeft = 'bottom-left',
  BottomRight = 'bottom-right',
}

export type TToasterPosition =
  | ToasterPosition.Static
  | ToasterPosition.TopCenter
  | ToasterPosition.TopFull
  | ToasterPosition.TopLeft
  | ToasterPosition.TopRight
  | ToasterPosition.BottomCenter
  | ToasterPosition.BottomFull
  | ToasterPosition.BottomLeft
  | ToasterPosition.BottomRight;

@Component({
  selector: 'c-toaster',
  templateUrl: './toaster.component.html',
  styleUrls: ['./toaster.component.scss']
})
export class ToasterComponent implements OnDestroy, OnInit, AfterContentChecked  {

  stateToasterSubscription: Subscription;
  positions = Object.values(ToasterPosition);
  toasts: QueryList<ViewContainerRef>;
  // toasts: QueryList<ComponentRef<ToastComponent>>;
  toastsDynamic: any[] = [];

  @Input() position: TToasterPosition = ToasterPosition.TopRight;
  @ViewChild(ToasterHostDirective, {static: true}) toasterHost: ToasterHostDirective;
  @ContentChildren(ToastComponent, {read: ViewContainerRef}) contentToasts!: QueryList<ViewContainerRef>;
  // @ContentChildren(ToastComponent,  {read: ComponentRef}) contentToasts!: QueryList<ComponentRef<ToastComponent>>;

  @HostBinding('class')
  get getClasses() {
    return this.hostClasses;
  }

  get hostClasses() {
    const classes = classNames(
      {
        toaster: true,
        [`toaster-${this.position}`]: this.position !== 'static' && !!this.position
      }
    );
    return classes;
  }

  constructor(
    private componentFactoryResolver: ComponentFactoryResolver,
    private hostElement: ElementRef,
    private renderer: Renderer2,
    private toasterService: ToasterService
  ) { }

  ngOnInit(): void {
    this.stateToasterSubscribe(true);
  }

  ngOnDestroy(): void {
    this.stateToasterSubscribe(false);
  }


  ngAfterContentChecked() {
    this.toasts = this.contentToasts;
  }

  private stateToasterSubscribe(subscribe: boolean = true) {
    if (subscribe) {
      this.stateToasterSubscription = this.toasterService.toasterState$.subscribe((state) => {
        if (state.show === false) {
          this.removeToast(state);
        }
        if (state.show === true && state.toast?.dynamic === undefined) {}
      });
    } else {
      this.stateToasterSubscription.unsubscribe();
    }
  }

  addToast(toast: any, options: any) {
    const factory = this.componentFactoryResolver.resolveComponentFactory(toast);
    const componentRef = this.toasterHost.viewContainerRef.createComponent(factory);
    this.toastsDynamic.push(componentRef);
    const index = this.toastsDynamic.indexOf(componentRef);
    for (const [key, value] of Object.entries(options)) {
      componentRef.instance[key] = value;
    }
    componentRef.instance['position'] = this.position;
    componentRef.instance['dynamic'] = true;
    componentRef.instance['index'] = index;
    componentRef.instance['show'] = true;
    componentRef.changeDetectorRef.detectChanges();
    return componentRef;
  }

  removeToast(state) {
    this.toastsDynamic?.forEach(item => {
      if (state.toast.dynamic && (item.instance === state.toast)) {
        item.destroy();
      }
    });

    this.toasts?.forEach(item => {
      if (item.element.nativeElement === state.toast.hostElement.nativeElement) {
        state.toast.show = false;
        setTimeout(() => {
          item.element.nativeElement.remove();
        }, 300);
      }
    });

    // this.contentToastsVcRef?.forEach(item => {
    //   if (item.element.nativeElement === state.toast.hostElement.nativeElement) {
    //     item?.element.nativeElement.remove();
    //   }
    // });
  }
}
