import {Component, ElementRef, HostBinding, Input, OnChanges, Optional, Renderer2, SimpleChanges} from '@angular/core';
import { Router } from '@angular/router';
import classNames from 'classnames';

import { INavData } from './sidebar-nav';
import {SidebarNavHelper} from './sidebar-nav.service';
import {SidebarService} from '../sidebar.service';
import {SidebarComponent} from '../sidebar.component';

@Component({
  selector: 'c-sidebar-nav',
  templateUrl: './sidebar-nav.component.html',
  styles: [
    ':host {flex: 1;}',
    '.c-sidebar-minimized :host {overflow: visible !important;}'
  ]
})
export class SidebarNavComponent implements OnChanges {
  @Input() navItems: INavData[] = [];
  @Input() dropdownMode: 'closeInactive' | 'noAction' | 'openActive' = 'closeInactive';
  @Input() dropdownItems: boolean;

  @HostBinding('class.c-sidebar-nav')
  get sidebarNavClass() {
    return !this.dropdownItems;
  }

  @HostBinding('class.c-sidebar-nav-dropdown-items')
  get sidebarNavDropdownItemsClass() {
    return this.dropdownItems;
  }

  @HostBinding('attr.role') role = 'nav';

  public navItemsArray: INavData[] = [];

  constructor(
    @Optional() public sidebar: SidebarComponent,
    public helper: SidebarNavHelper,
    public router: Router,
    private renderer: Renderer2,
    private hostElement: ElementRef,
    private sidebarService: SidebarService
  ) { }

  public ngOnChanges(changes: SimpleChanges): void {
    // console.log('sidebar-nav changes', changes);
    this.navItemsArray = Array.isArray(this.navItems) ? this.navItems.slice() : [];
  }

  public hideMobile() {
    // todo: proper scrollIntoView() after NavigationEnd
    // setTimeout(() => {
    //   console.log('scroll')
    //   this.hostElement.nativeElement.scrollIntoView();
    // }, 1000);
    // this.sidebarService.toggle({open: false, breakpoint: ''});
    if (this.sidebar && this.sidebar.hideOnMobileClick && this.sidebar.isOnMobile) {
      this.sidebarService.toggle({open: false, sidebar: this.sidebar});
    }
  }
}
