import {Component, HostBinding, Inject, Input, OnDestroy, OnInit} from '@angular/core';
import {AccordionComponent} from './accordion.component';

@Component({
  selector: 'c-accordion-group',
  templateUrl: './accordion-group.component.html',
  styleUrls: ['./accordion-group.component.scss']
})
export class AccordionGroupComponent implements OnInit, OnDestroy {

  @Input() isOpen = false;
  @Input() groupClass = '';

  @HostBinding('class.card') cardClass = true;

  accordion: AccordionComponent;

  constructor(
    @Inject(AccordionComponent) accordion: AccordionComponent,
    ) {
    // this.groupClass = `${this.groupClass} c-p-0 c-m-0 c-card`;
    this.accordion = accordion;
  }

  ngOnInit() {
    this.accordion.addGroup(this);
  }

  ngOnDestroy(): void {
    this.accordion.removeGroup(this);
  }

  toggleGroup() {
    this.isOpen = !this.isOpen;
    this.accordion.closeOtherGroups(this);
  }
}
