import {
  Component,
  HostBinding,
  Input,
  OnInit,
} from '@angular/core';
import classNames from 'classnames';

@Component({
  selector: 'c-nav',
  templateUrl: './nav.component.html',
  styleUrls: ['./nav.component.scss']
})
export class NavComponent implements OnInit {

  @Input() variant: '' | 'tabs' | 'pills' = '';
  @Input() fill: boolean;
  @Input() justified: boolean;
  @Input() vertical: boolean | string;
  @Input() inCard: boolean;

  @HostBinding('class')
  get getClasses() {
    return this.hostClasses;
  }

  get hostClasses() {
    const verticalSuffix = this.vertical === true ? '' : `-${this.vertical}`;
    const classes = classNames(
      {
        nav: true,
        [`nav-${this.variant}`]: this.variant,
        [`flex${verticalSuffix}-column`]: this.vertical,
        'nav-fill': this.fill,
        'nav-justified': this.justified,
        [`card-header-${this.variant}`]: this.inCard && this.variant
      },
    );
    return classes;
  }

  constructor() { }

  ngOnInit(): void {}

}
