import {
  Directive,
  ElementRef,
  HostListener,
  OnDestroy,
  OnInit,
} from '@angular/core';
import { Subscription } from 'rxjs';
import { OutClickService } from './out-click.service';

/**
 * Detects click outside the element
 */
@Directive({
  selector: '[cOutClick]',
  exportAs: 'cOutClick',
})
export class OutClickDirective implements OnInit, OnDestroy {
  private outClickSubscription: Subscription;

  constructor(
    private elementRef: ElementRef,
    private outClickService: OutClickService
  ) {}

  @HostListener('document:click', ['$event'])
  public onDocumentClick($event: MouseEvent): void {
    const targetElement = $event.target as HTMLElement;
    // $event.preventDefault();
    // Check if the click was outside the element
    if (targetElement && !this.elementRef.nativeElement.contains(targetElement)) {
      this.outClickService.onClick({ event: $event });
    }
  }

  ngOnInit() {
    this.outClickSubscription = this.outClickService.outClick$.subscribe(
      (message) => {
        // todo
        // console.log('outClick', message);
      }
    );
  }

  ngOnDestroy() {
    this.outClickSubscription.unsubscribe();
  }
}
