import {Directive, Host, OnInit} from '@angular/core';
import {BreadcrumbService} from './breadcrumb.service';
import {BreadcrumbComponent} from './breadcrumb.component';

@Directive({
  selector: '[cBreadcrumbRouter]',
  exportAs: 'cBreadcrumbRouter'
})
export class BreadcrumbDirective implements OnInit {

  public breadcrumbs;

  constructor(
    @Host() public breadcrumbComponent: BreadcrumbComponent,
    public service: BreadcrumbService,
  ) { }

  ngOnInit(): void {
    this.breadcrumbComponent.breadcrumbs = this.service.breadcrumbs$;
  }
}
