import { Directive, HostBinding, Input } from '@angular/core';
import classNames from 'classnames';

@Directive({
  selector: '[cNavLink]',
})
export class NavLinkDirective {

  @Input() disabled: null | string | boolean = null;
  @Input() tabindex: string | null = null;
  @Input() active: boolean;

  @HostBinding('attr.aria-disabled')
  get isDisabled(): boolean {
    return (this.disabled !== 'false' && this.disabled !== false && this.disabled !== null) ? this.disabled as boolean : null;
  }

  @HostBinding('attr.tabindex')
  get getTabindex(): string {
    return this.disabled ? null : this.tabindex != null ? this.tabindex : '0';
  }

  @HostBinding('class')
  get getClasses() {
    return this.hostClasses;
  }

  get hostClasses() {
    const classes = classNames(
      {
        'nav-link': true,
        disabled: this.disabled,
        active: this.active
      },
    );
    return classes;
  }

  constructor() { }

}
