import {Component, HostBinding, Input, OnDestroy, OnInit} from '@angular/core';
import { Subscription } from 'rxjs';

import {SidebarService} from '../../sidebar/sidebar.service';

@Component({
  selector: 'c-header-brand',
  templateUrl: './header-brand.component.html'
})
export class HeaderBrandComponent implements OnInit, OnDestroy {

  @Input() routerLink: any[] | string = '';
  @Input() minimized = false;

  headerBrandImg: boolean;
  private stateToggleSubscription: Subscription;

  constructor(
    private sidebarService: SidebarService,
  ) { }

  ngOnInit(): void {
    this.stateToggleSubscribe();
  }

  ngOnDestroy(): void {
    this.stateToggleSubscribe(false);
  }

  minimize(force?: boolean | 'toggle'): void {
    this.minimized = (force === 'toggle') ? !this.minimized : force;
  }

  private stateToggleSubscribe(subscribe: boolean = true) {
    // console.log('stateToggleSubscribe', subscribe);
    if (subscribe) {
      this.stateToggleSubscription = this.sidebarService.sidebarState$.subscribe((state) => {
        if (!state.id) {
          if ('minimize' in state) {
            this.minimize(state.minimize);
          }
        }
      });
    } else {
      this.stateToggleSubscription.unsubscribe();
    }
  }
}
