import { Component, HostBinding, Input, OnInit } from '@angular/core';
import classNames from 'classnames';

@Component({
  selector: 'c-widget-icon',
  templateUrl: './widget-icon.component.html',
  styleUrls: ['./widget-icon.component.scss']
})
export class WidgetIconComponent implements OnInit {

  @Input() color: string;
  @Input() header: string;
  @Input() text: string;
  @Input() iconPadding = true;

  @HostBinding('class')
  get getClasses() {
    return this.hostClasses;
  }

  get hostClasses() {
    const classes = classNames(
      {
        card : true,
      },
    );
    return classes;
  }

  constructor() { }

  ngOnInit(): void {
  }

  get cardBodyClasses() {
    const classes = classNames(
      {
        'card-body': true,
        'd-flex': true,
        'align-items-center': true,
        [`p-${this.iconPadding ? '3' : '0'}`]: true,
      },
    );
    return classes;
  }

  get bodySlotClasses() {
    const classes = classNames(
      {
        'mfe-3': true,
        'text-white': true,
        [`bg-${this.color}`]: this.color,
        [`p-${this.iconPadding ? '3' : '4'}`]: true,
      },
    );
    return classes;
  }

  get headerClasses() {
    const classes = classNames(
      {
        'text-value': true,
        [`text-${this.color}`]: this.color
      },
    );
    return classes;
  }
}
