import {
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  ElementRef,
  HostBinding,
  HostListener,
  Input,
  OnInit,
  ViewChild,
} from '@angular/core';
import classNames from 'classnames';

import { CarouselState } from '../carousel-state';

@Component({
  selector: 'c-carousel-control',
  templateUrl: './carousel-control.component.html',
  styleUrls: ['./carousel-control.component.scss'],
})
export class CarouselControlComponent implements OnInit, AfterViewInit {
  @Input() direction: 'prev' | 'next' = 'next';

  @ViewChild('content') content: ElementRef;

  hasContent = true;

  @HostBinding('attr.role')
  get hostRole(): string {
    return 'button';
  }

  @HostBinding('class')
  get getClasses() {
    return this.hostClasses;
  }

  get carouselControlClass() {
    return `carousel-control-${this.direction}`;
  }

  get carouselControlIconClass() {
    return `carousel-control-${this.direction}-icon`;
  }

  get hostClasses() {
    const classes = classNames(this.carouselControlClass);
    return classes;
  }

  @HostListener('keyup', ['$event'])
  onKeyUp($event: KeyboardEvent) {
    if ($event.key === 'Enter') {
      this.play();
    }
    if ($event.key === 'ArrowLeft') {
      this.play('prev');
    }
    if ($event.key === 'ArrowRight') {
      this.play('next');
    }
  }

  @HostListener('click', ['$event'])
  public onClick($event: MouseEvent): void {
    this.play();
  }

  constructor(
    private changeDetector: ChangeDetectorRef,
    private carouselState: CarouselState
  ) {}

  ngOnInit(): void {}

  ngAfterViewInit() {
    this.hasContent = this.content.nativeElement.childNodes.length as boolean;
    this.changeDetector.detectChanges();
  }

  private play(direction = this.direction) {
    const nextIndex = this.carouselState.direction(direction);
    this.carouselState.state = { activeItemIndex: nextIndex };
  }
}
