import {AfterContentChecked, Component, HostBinding, Input, OnDestroy, OnInit} from '@angular/core';
import {Subscription} from 'rxjs';
import {TabsetService} from '../tabset.service';

@Component({
  selector: 'c-tab-pane',
  templateUrl: './tab-pane.component.html',
  styleUrls: ['./tab-pane.component.scss']
})
export class TabPaneComponent implements OnDestroy, OnInit {

  @Input()
  set active(active: boolean) {
    this._tabActive = active;
    this._tabShow = active;
  }
  get active() {
    return this._tabActive;
  }

  public indexKey: number;
  public fade = true;
  private activeTabSubscription: Subscription;

  @HostBinding('class.tab-pane') _tabPane = true;
  @HostBinding('class.active') _tabActive = true;
  @HostBinding('class.show') _tabShow = false;
  @HostBinding('class.fade') _tabFade = this.fade;

  constructor(
    private tabsetService: TabsetService
  ) { }

  ngOnInit() {
    this.activeTabSubscribe();
  }

  ngOnDestroy() {
    this.activeTabUnsubscribe();
  }

  private activeTabSubscribe() {
    this.activeTabSubscription = this.tabsetService.activeTab$.subscribe((activeTab) => {
      setTimeout(() => {
        this.active = activeTab.indexKey === this.indexKey;
      });
    });
  }

  private activeTabUnsubscribe() {
    this.activeTabSubscription.unsubscribe();
  }
}
