import {
  AfterViewInit,
  Component,
  HostBinding,
  Input,
  OnDestroy,
  OnInit,
} from '@angular/core';

import { Subscription } from 'rxjs';
import classNames from 'classnames';

import { CarouselService } from '../carousel.service';

@Component({
  selector: 'c-carousel-item',
  templateUrl: './carousel-item.component.html',
  styleUrls: ['./carousel-item.component.scss'],
})
export class CarouselItemComponent implements OnInit, OnDestroy, AfterViewInit {
  index: number;
  private _active: boolean;
  private carouselIndexSubscription: Subscription;

  @Input()
  set active(active) {
    this._active = active;
  }
  get active() {
    return this._active;
  }

  @HostBinding('class')
  get getClasses() {
    return this.hostClasses;
  }

  get hostClasses() {
    const classes = classNames({
      'carousel-item': true,
      active: this.active,
    });
    return classes;
  }

  constructor(
    private carouselService: CarouselService,
  ) {}

  ngOnInit(): void {}

  ngOnDestroy() {
    this.carouselStateSubscribe(false);
  }

  ngAfterViewInit() {
    setTimeout(() => {
      this.carouselStateSubscribe();
    });
  }

  private carouselStateSubscribe(subscribe: boolean = true) {
    if (subscribe) {
      this.carouselIndexSubscription = this.carouselService.carouselIndex$.subscribe((nextIndex) => {
        if ('active' in nextIndex) {
          this.active = nextIndex.active === this.index;
        }
      });
    } else {
      this.carouselIndexSubscription.unsubscribe();
    }
  }
}
