import {Component, HostBinding} from '@angular/core';

import { AccordionGroupComponent } from './accordion-group.component';

@Component({
  selector: 'c-accordion',
  template: '<ng-content></ng-content>',
  styleUrls: ['./accordion.component.scss']
})
export class AccordionComponent {

  @HostBinding('class.accordion') accordionClass = true;

  groups: AccordionGroupComponent[] = [];

  closeOtherGroups(openGroup: AccordionGroupComponent): void {

    this.groups.forEach((group: AccordionGroupComponent) => {
      if (group !== openGroup) {
        group.isOpen = false;
      }
    });
  }

  addGroup(group): void {
    this.groups.push(group);
  }

  removeGroup(group): void {
    const index = this.groups.indexOf(group);
    if (index !== -1) {
      this.groups.splice(index, 1);
    }
  }
}
