import {Component, HostBinding, Input} from '@angular/core';

@Component({
  selector: 'c-tablist',
  templateUrl: './tab-list.component.html',
  styleUrls: ['./tab-list.component.scss']
})
export class TabListComponent {

  @Input()
  set variant(variant: 'tabs' | 'pills') {
    this._navTabsClass = variant !== 'pills';
    this._navPillsClass = variant === 'pills';
  }

  @Input()
  set vertical(vertical: boolean) {
    this._navFlexColumn = !!vertical;
    this._navH100 = !!vertical;
  }

  @Input()
  set fill(fill: boolean) {
    this._navFill = !!fill;
  }

  @Input()
  set justified(justified: boolean) {
    this._navJustified = !!justified;
  }

  @HostBinding('class.nav') _navClass = true;
  @HostBinding('class.nav-tabs') _navTabsClass;
  @HostBinding('class.nav-pills') _navPillsClass;
  @HostBinding('class.nav-fill') _navFill;
  @HostBinding('class.nav-justified') _navJustified;
  @HostBinding('class.flex-column') _navFlexColumn;
  @HostBinding('class.h-100') _navH100;

  constructor(
  ) { }
}
