import { Component, HostBinding, Input, OnInit } from '@angular/core';

import { chkBoolProp } from '../../shared/check-bool-prop';

@Component({
  // selector: 'c-input-group-addon',
  templateUrl: './input-group-addon.component.html',
  styleUrls: ['./input-group-addon.component.scss'],
})
export class InputGroupAddonComponent implements OnInit {
  @Input() prepend: boolean;

  @HostBinding('class')
  get getClasses() {
    return this.hostClasses;
  }

  get hostClasses() {
    const addon = chkBoolProp(this.prepend) ? 'prepend' : 'append';

    const classes = {
      [`input-group-${addon}`]: true,
    };
    return classes;
  }
  constructor() {}

  ngOnInit(): void {}
}

@Component({
  selector: 'c-input-group-prepend',
  templateUrl: './input-group-addon.component.html',
  styleUrls: ['./input-group-addon.component.scss'],
})
export class InputGroupPrependComponent extends InputGroupAddonComponent {
  prepend = true;
}

@Component({
  selector: 'c-input-group-append',
  templateUrl: './input-group-addon.component.html',
  styleUrls: ['./input-group-addon.component.scss'],
})
export class InputGroupAppendComponent extends InputGroupAddonComponent {
  prepend = false;
}
