import {
  AfterViewInit,
  Directive,
  ElementRef,
  HostBinding,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Renderer2,
  SimpleChanges,
} from '@angular/core';
import {Subscription} from 'rxjs';

import {OutClickService} from '../shared';
import {DropdownService} from './dropdown.service';
import {DropdownState, DropdownStateInterface} from './dropdown.state';

@Directive({
  selector: '[cDropdown], c-dropdown',
  exportAs: 'cDropdown',
  providers: [ DropdownService, DropdownState ],
})
export class DropdownDirective implements OnInit, OnChanges, OnDestroy, AfterViewInit {

  @Input('cDropdown') direction: 'down' | 'left' | 'right' | 'up' = 'down';
  @Input() cDropdownAlign: '' | 'right' = '';
  // @Input() cDropdownShow = false;
  // set cDropdownShow(cDropdownShow) {
  //   this.state = this.dropdownState.setState({show: cDropdownShow});
  //   this.show(cDropdownShow);
  // }
  // get cDropdownShow(): boolean {
  //   this.state = this.dropdownState.getState();
  //   return this.state.show;
  // }

  private stateToggleSubscription: Subscription;
  private outClickSubscription: Subscription;
  private state: DropdownStateInterface;

  @Input()
  @HostBinding('attr.aria-expanded') cDropdownShow: boolean;

  constructor(
    private renderer: Renderer2,
    private hostElement: ElementRef,
    private outClickService: OutClickService,
    private dropdownService: DropdownService,
    private dropdownState: DropdownState
  ) { }

  ngOnInit() {
    const state = { show: this.cDropdownShow, align: this.cDropdownAlign, direction: this.direction };
    this.state = this.dropdownState.setState(state);
    // console.log('cDropdown OnInit', this.state, this.dropdownState.getState());
    const dropdownClass = `drop${this.direction || 'down'}`;
    this.renderer.addClass(this.hostElement.nativeElement, dropdownClass);
    this.stateToggleSubscribe();
    this.outClickSubscribe();
  }

  ngOnDestroy(): void {
    this.stateToggleSubscribe(false);
    this.outClickSubscribe(false);
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.direction) {
      this.renderer.removeClass(this.hostElement.nativeElement, `drop${changes.direction.previousValue || 'down'}`);
      this.renderer.addClass(this.hostElement.nativeElement, `drop${changes.direction.currentValue || 'down'}`);
    }
    if (changes.cDropdownShow) {
      const state = { show: changes.cDropdownShow.currentValue };
      this.state = this.dropdownState.setState(state);
      this.dropdownService.toggle(this.state);
    }
  }

  ngAfterViewInit(): void {
    // console.log('cDropdown ngAfterViewInit', this.state);
    this.cDropdownShow = this.state.show;
    this.dropdownService.toggle(this.state);
  }

  private stateToggleSubscribe(subscribe: boolean = true) {
    if (subscribe) {
      this.stateToggleSubscription = this.dropdownService.dropdownState$.subscribe((state) => {
        if ('show' in state) {
          this.show(state.show);
        }
      });
    } else {
      this.stateToggleSubscription.unsubscribe();
    }
  }

  show(show) {
    this.cDropdownShow = show;
    this.state = this.dropdownState.setState({show});
    show ?
      this.renderer.addClass(this.hostElement.nativeElement , 'show') :
      this.renderer.removeClass(this.hostElement.nativeElement , 'show');
  }

  toggle() {
    this.show(!this.state.show);
  }

  private outClickSubscribe(subscribe: boolean = true) {
    if (subscribe) {
      this.outClickSubscription = this.outClickService.outClick$.subscribe(message => {
        // console.log('cOutClick', message)
        if (message.event) {
          this.autoClose(message.event);
        }
      });
    } else {
      this.outClickSubscription.unsubscribe();
    }
  }

  autoClose(e) {
    // console.log('autoClose', e);
    const state = this.dropdownState.getState();
    if (state.show) {
      // console.log('contains', this.hostElement.nativeElement.contains(e.target), e.target );
      // if (!e.target.closest('[cDropdownToggle]')) {
      if (!this.hostElement.nativeElement.contains(e.target.closest('[cDropdownToggle]')) ) {
        this.cDropdownShow = false;
        this.dropdownService.toggle({show: false});
      }
    }
  }
}

