import {
  AfterContentInit,
  Component,
  ContentChild,
  HostBinding,
  Input,
  OnInit,
} from '@angular/core';
import classNames from 'classnames';

import { EmbedItemDirective } from './embed-item.directive';

@Component({
  selector: 'c-embed',
  templateUrl: './embed.component.html',
  styleUrls: ['./embed.component.scss'],
})
export class EmbedComponent implements OnInit, AfterContentInit {
  @Input() ratio: '21by9' | '16by9' | '4by3' | '1by1' = '16by9';

  @HostBinding('class')
  get getClasses() {
    return this.hostClasses;
  }

  get hostClasses() {
    const classes = classNames({
      'embed-responsive': true,
      [`embed-responsive-${this.ratio}`]: !!this.ratio,
    });
    return classes;
  }

  @ContentChild(EmbedItemDirective) embedItem!: EmbedItemDirective;

  constructor() {}

  ngOnInit(): void {}

  ngAfterContentInit() {
    // console.log('embedItem', this.embedItem);
  }
}
