import {Directive, HostListener, Input} from '@angular/core';

import {SidebarService} from './sidebar.service';

/**
 * Allows the sidebar to be toggled via click.
 */
@Directive({
  selector: '[cSidebarToggle]',
  exportAs: 'cSidebarToggle',
})
export class SidebarToggleDirective {

  @Input('cSidebarToggle') id: string;

  constructor(
    private sidebarService: SidebarService
  ) {}

  @HostListener('click', ['$event'])
  toggleOpen($event: any) {
    $event.preventDefault();
    // const {breakpoint, aside} = this;
    // this.sidebarService.toggle({ open: undefined, breakpoint: breakpoint, aside: aside, id: this.id });
    this.sidebarService.toggle({ open: 'toggle', id: this.id });
  }
}
