import {
  AfterContentChecked, AfterViewChecked, ContentChild, ViewChild,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  HostBinding, HostListener,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  Renderer2,
  SimpleChanges
} from '@angular/core';
import classNames from 'classnames';
import { Subscription } from 'rxjs';
import { filter } from 'rxjs/operators';
import { ToasterService } from '../toaster/toaster.service';
import { TToasterPosition } from '../toaster/toaster.component';
import { animate, AnimationEvent, state, style, transition, trigger } from '@angular/animations';
// import { ToastHeaderComponent } from '../toast-header/toast-header.component';

type AnimateType = ( 'hide' | 'show' );

@Component({
  selector: 'c-toast',
  templateUrl: './toast.component.html',
  styleUrls: ['./toast.component.scss'],
  animations: [
    trigger('fadeInOut', [
      state('show', style({opacity: 1})),
      state('void', style({opacity: 0, height: 0, padding: 0, border: 0, margin: 0 })),
      state('hide', style({opacity: 0, height: 0, padding: 0, border: 0, margin: 0 })),
      transition('show => *', [
        animate('{{ time }} {{ easing }}', style({opacity: 0})),
        animate('{{ time }} {{ easing }}', style({height: 0, padding: 0, border: 0, margin: 0})),
      ], {
        params: { time: '300ms', easing: 'ease-out' }
      }),
      transition('* => show', [
        animate('{{ time }} {{ easing }}', style({opacity: 1, height: '*', padding: '*', border: '*', margin: '*'})),
        // animate('{{ time }} {{ easing }}', style({opacity: '*'}))
      ], {
        params: { time: '300ms', easing: 'ease-in' }
      })
    ])
  ]
})
export class ToastComponent implements OnInit, OnDestroy, OnChanges {

  private timerId: any;
  private clockId: any;
  private clockTimerId: any;
  private _clock: number;
  private state: any;
  private stateToasterSubscription: Subscription;

  dynamic: boolean;
  position: TToasterPosition;

  set clock(value) {
    this._clock = value;
    this.timer.emit(this._clock);
  }

  get clock() {
    return this._clock;
  }

  @Input() autohide = true;
  @Input() color: string;
  @Input() delay = 5000;
  @Input() fade = true;
  @Input() show: boolean;
  @Input() index: number;

  // @ContentChild(ToastHeaderComponent) toastHeaderContent!: ToastHeaderComponent;
  // @ViewChild(ToastHeaderComponent) toastHeaderView!: ToastHeaderComponent;

  @Output() shown = new EventEmitter<void>();
  @Output() hidden = new EventEmitter<void>();
  @Output() timer: EventEmitter<number> = new EventEmitter();

  @HostBinding('@.disabled')
  get animationDisabled() {
    return !this.fade;
  }

  @HostBinding('@fadeInOut')
  get animateType(): AnimateType {
    // return this.animate;
    return this.show ? 'show' : 'hide';
  }

  // @HostListener('@fadeInOut.start', ['$event']) onAnimationStart($event) {
  //   this.onAnimationEvent($event);
  // }
  //
  // @HostListener('@fadeInOut.done', ['$event']) onAnimationDone($event) {
  //   this.onAnimationEvent($event);
  // }

  @HostListener('mouseover') onMouseOver() {
    this.clearTimer();
    this.clearClock();
  }

  @HostListener('mouseout') onMouseOut() {
    this.setTimer();
  }

  @HostBinding('class')
  get getClasses() {
    return this.hostClasses;
  }

  get hostClasses() {
    const classes = classNames(
      {
        toast: true,
        [`toast-${this.color}`]: !!this.color,
        // show: !!this.show
      }
    );
    return classes;
  }

  constructor(
    public hostElement: ElementRef,
    public renderer: Renderer2,
    public ref: ChangeDetectorRef,
    public toasterService: ToasterService
  ) {
  }

  ngOnInit(): void {
    if (this.show) {
      this.toasterService.setState({toast: this, show: this.show, position: this.position});
      this.setTimer();
    }
    this.stateToasterSubscribe(true);
  }

  ngOnDestroy() {
    this.clearTimer();
    this.clearClock();
    this.stateToasterSubscribe(false);
  }

  public ngOnChanges(changes: SimpleChanges): void {
    if (changes.color) {
      this.renderer.removeClass(this.hostElement.nativeElement, `toast-${changes.color.previousValue}`);
    }
    if (changes.show) {
      this.toasterService.setState({toast: this, show: this.show, position: this.position});
    }
  }

  setTimer() {
    this.clearTimer();
    if (this.autohide) {
      this.timerId = this.delay > 0 ? setTimeout(() => this.onClose(), this.delay) : null;
      this.setClock();
    }
  }

  clearTimer() {
    clearTimeout(this.timerId);
    this.timerId = null;
  }

  onClose() {
    this.clearTimer();
    this.show = false;
    this.hidden.emit();
    this.toasterService.setState({toast: this, show: this.show, position: this.position});
  }

  setClock() {
    this.clearClock();
    this.clock = 0;
    this.clockId = setInterval(() => {
      this.clock += 1;
    }, 1000);
    this.clockTimerId = setTimeout(() => {
      this.clearClock();
    }, this.delay);
  }

  clearClock() {
    clearTimeout(this.clockTimerId);
    clearInterval(this.clockId);
    this.clockId = null;
  }

  private stateToasterSubscribe(subscribe: boolean = true) {
    if (subscribe) {
      const filtered = this.toasterService.toasterState$.pipe(filter(state => state.toast === this));
      this.stateToasterSubscription = filtered.subscribe((state) => {
        this.state = state.toast;
      });
    } else {
      this.stateToasterSubscription.unsubscribe();
    }
  }
  //
  // onAnimationEvent(event: AnimationEvent) {
  //   console.warn(event);
  //   if (event.phaseName === 'done' && event.toState === 'hide') {
  //     // this.hide = false;
  //   }
  //   if (event.phaseName === 'start' && event.toState === 'show') {
  //     // this.hide = true;
  //   }
  // }
}
