import {AfterContentChecked, AfterContentInit, Component, ContentChildren, HostBinding, OnInit, QueryList} from '@angular/core';
import {TabPaneComponent} from '../tab-pane/tab-pane.component';

@Component({
  selector: 'c-tab-content',
  templateUrl: './tab-content.component.html',
  styleUrls: ['./tab-content.component.scss']
})
export class TabContentComponent implements OnInit, AfterContentChecked {

  private _hasPanes = false;

  @HostBinding('class.tab-content') _tabContent = true;
  @HostBinding('attr.hidden')
  public set hasPanes(hasPanes: boolean) {
    this._hasPanes = hasPanes;
  }
  public get hasPanes() {
    return this._hasPanes ? null : true;
  }
  @ContentChildren(TabPaneComponent) panes: QueryList<TabPaneComponent>;

  constructor() { }

  ngOnInit() {
  }

  ngAfterContentChecked(): void {
    this.hasPanes = this.panes.length > 0;
    // console.log('ngAfterContentInit panes', this.panes);
  }

}
