import {Directive, ElementRef, HostBinding, OnDestroy, OnInit, Renderer2} from '@angular/core';
import {Subscription} from 'rxjs';
import {DropdownState} from './dropdown.state';
import {DropdownService} from './dropdown.service';

@Directive({
  selector: '[cDropdownMenu], c-dropdown-menu',
  exportAs: 'cDropdownMenu'
})
export class DropdownMenuDirective implements OnInit, OnDestroy {

  @HostBinding('attr.role') role = 'menu';

  @HostBinding('class.dropdown-menu') dropdownMenu = true;

  private stateToggleSubscription: Subscription;
  private readonly host;

  constructor(
    private renderer: Renderer2,
    private hostElement: ElementRef,
    private dropdownState: DropdownState,
    private dropdownService: DropdownService,
  ) {
    this.host = this.hostElement.nativeElement;
  }

  ngOnInit() {
    // this.renderer.addClass(this.host, 'py-0');
    this.stateToggleSubscribe();
  }

  ngOnDestroy(): void {
    this.stateToggleSubscribe(false);
  }

  show(show) {
    const className = 'show';
    // console.log('cDropdownMenu show', show);
    show ?
      this.renderer.addClass(this.host, className) :
      this.renderer.removeClass(this.host, className);
  }

  align(align) {
    if (align) {
      const className = `dropdown-menu-${align}`;
      this.renderer.addClass(this.host, className);
    }
  }

  // align(align) {
  //   const aligns = Object.keys(align);
  //   console.log('aligns', align, aligns)
  //   aligns.forEach(key => {
  //     console.log('keys', key)
  //     const breakpoint = checkBreakpoint(align[key], validBreakpoints) ? `-${align[key]}` : '';
  //     const className = `dropdown-menu${breakpoint}-${key}`;
  //     align[key] ?
  //       this.renderer.addClass(this.host, className) :
  //       this.renderer.removeClass(this.host, className);
  //   });
  // }

  private stateToggleSubscribe(subscribe: boolean = true) {
    if (subscribe) {
      this.stateToggleSubscription = this.dropdownService.dropdownState$.subscribe((state) => {
        // console.log('subscribed', state);
        if ('show' in state) {
          this.show(state.show);
        }
        if ('align' in state) {
          this.align(state.align);
        }
      });
    } else {
      this.stateToggleSubscription.unsubscribe();
    }
  }
}
