import {Component, Input} from '@angular/core';
import {Router} from '@angular/router';

import {SidebarNavHelper} from './sidebar-nav.service';

@Component({
  selector: 'c-sidebar-nav-items',
  template: `
    <ng-container *ngFor="let item of items">
      <ng-container [ngSwitch]="helper.itemType(item)">
        <c-sidebar-nav-dropdown
          *ngSwitchCase="'dropdown'"
          [item]="item"
          [class.c-show]="helper.isActive(router, item)"
          [ngClass]="item | cSidebarNavItemClass"
          cSidebarNavDropdown
          routerLinkActive="c-show">
        </c-sidebar-nav-dropdown>
        <c-sidebar-nav-divider
          *ngSwitchCase="'divider'"
          [item]="item"
          [ngClass]="item | cSidebarNavItemClass"
          [cHtmlAttr]="item.attributes">
        </c-sidebar-nav-divider>
        <c-sidebar-nav-title
          *ngSwitchCase="'title'"
          [item]="item"
          [ngClass]="item | cSidebarNavItemClass"
          [cHtmlAttr]="item.attributes">
        </c-sidebar-nav-title>
        <c-sidebar-nav-label
          *ngSwitchCase="'label'"
          [item]="item"
          [ngClass]="item | cSidebarNavItemClass">
        </c-sidebar-nav-label>
        <ng-container
          *ngSwitchCase="'empty'">
        </ng-container>
        <c-sidebar-nav-link
          *ngSwitchDefault
          [item]="item"
          [ngClass]="item | cSidebarNavItemClass"
        >
        </c-sidebar-nav-link>
      </ng-container>
    </ng-container>
  `
})
export class SidebarNavItemsComponent {
  protected _items: any;

  @Input()
  set items(items: Array<any>) {
    this._items = [...items];
  }
  get items(): Array<any> {
    return this._items;
  }

  constructor(
    public router: Router,
    public helper: SidebarNavHelper
  ) {}
}
