import { Component, HostBinding, Input, OnInit } from '@angular/core';
import classNames from 'classnames';

@Component({
  selector: 'c-widget-brand',
  templateUrl: './widget-brand.component.html',
  styleUrls: ['./widget-brand.component.scss']
})
export class WidgetBrandComponent implements OnInit {

  @Input() color: string;
  @Input() rightHeader: string;
  @Input() leftHeader: string;
  @Input() rightFooter: string;
  @Input() leftFooter: string;
  @Input() addHeaderClasses: string | object | string[];

  // @ContentChild('slot=body') todo

  @HostBinding('class')
  get getClasses() {
    return this.hostClasses;
  }

  get hostClasses() {
    const classes = classNames(
      {
        card : true,
      },
    );
    return classes;
  }

  constructor() { }

  ngOnInit(): void {
  }

  get headerClasses() {
    const classes = classNames(
      {
        'card-header': true,
        'content-center': true,
        'text-white': true,
        'p-0': true,
        [`bg-${this.color}`]: !!this.color,
      },
      this.addHeaderClasses
    );
    return classes;
  }

}

