import {
  AfterViewInit,
  Component,
  HostBinding, HostListener,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Renderer2,
  SimpleChanges,
  ViewChild
} from '@angular/core';

import {TabsetService} from '../tabset.service';
import {Subscription} from 'rxjs';

@Component({
  selector: 'c-tab',
  templateUrl: './tab.component.html',
  styleUrls: ['./tab.component.scss']
})
export class TabComponent implements AfterViewInit, OnChanges, OnDestroy, OnInit {

  @Input()
  set active(active: boolean) {
    this.classMap.active = active;
    // this.classMap = Object.assign({}, this.classMap);
    // if (active) {
    //  this.tabsetService.setActiveTab(this);
    // }
  }
  get active() {
    return this.classMap.active;
  }
  @Input()
  set disabled(disabled: boolean) {
    this.classMap.disabled = disabled;
    // this.classMap = Object.assign({}, this.classMap);
  }
  get disabled() {
    return this.classMap.disabled;
  }

  public indexKey: number;

  activeTabSubscription: Subscription;

  classMap: { [key: string]: boolean } = {};

  @HostBinding('class.nav-item') _navItemClass = true;
  @ViewChild('link', {static: true}) _link;

  @HostListener('click', ['$event'])
  onClick(e) {
    e.preventDefault();
    if (!this.disabled) {
      this.active = true;
      this.tabsetService.setActiveTab(this);
    }
  }

  constructor(
    private renderer: Renderer2,
    private tabsetService: TabsetService,
  ) {}

  ngOnInit() {
    this.classMap = {
      'nav-link': true,
      active: this.active,
      disabled: this.disabled
    };
  }
  ngOnDestroy() {
    this.activeTabUnsubscribe();
  }

  ngAfterViewInit(): void {
    // console.log('_link', this._link);
    this.activeTabSubscribe();
  }

  ngOnChanges(changes: SimpleChanges): void {
    // console.log('ngOnChanges tab', this);
    this.classMap = {
      active: changes.active ? changes.active.currentValue : this.active,
      disabled: changes.disabled ? changes.disabled.currentValue : this.disabled
    };
    this.classMap = Object.assign(Object.create(null), this.classMap);
  }

  private activeTabSubscribe() {
    this.activeTabSubscription = this.tabsetService.activeTab$.subscribe((activeTab) => {
      this.active = this.indexKey === activeTab.indexKey;
      this.classMap.active = this.indexKey === activeTab.indexKey;
    });
  }
  private activeTabUnsubscribe() {
    this.activeTabSubscription.unsubscribe();
  }
}
