import {
  Directive, DoCheck, ElementRef, EventEmitter, HostBinding, Input, OnChanges, OnDestroy, OnInit, Output, SimpleChanges
} from '@angular/core';
import {AnimationBuilder, AnimationPlayer, useAnimation} from '@angular/animations';
import classNames from 'classnames';

import {collapseAnimation, expandAnimation} from './collapse.animations';

@Directive({
  selector: '[cCollapse]',
  exportAs: 'cCollapse',
})
export class CollapseDirective implements OnInit, OnChanges, OnDestroy, DoCheck {

  @Input() navbar: boolean;
  @Input() duration = '400ms';
  @Input() transition = 'ease-in-out';
  @Input() show: boolean;
  @Input() animate = true;

  @Output() collapseChange = new EventEmitter<string>();

  private _show = false;
  _animate = this.animate;

  private player: AnimationPlayer;
  private readonly host: HTMLElement;

  @HostBinding('class')
  get getClasses() {
    return this.hostClasses;
  }

  get hostClasses() {
    const classes = classNames({
      'navbar-collapse': !!this.navbar,
      show: !!this.show,
    });
    return classes;
  }

  constructor(
    private hostElement: ElementRef,
    private animationBuilder: AnimationBuilder
  ) {
    this.host = this.hostElement.nativeElement;
  }

  ngOnInit() {
    this._show = this.show;
  }

  ngOnDestroy(): void {
    this.destroyPlayer();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.show) {
      (!changes.show.firstChange || !changes.show.currentValue) && this.toggle(changes.show.currentValue);
    }
  }

  ngDoCheck() {
    if (this._show !== this.show) { this.toggle(); }
  }

  toggle(show = this.show) {
    this._show = show;
    this.createPlayer(show);
    this.player.play();
  }

  destroyPlayer() {
    if (this.player) {
      this.player.destroy();
    }
  }

  createPlayer(show = this.show) {
    if (this.player) {
      this.destroyPlayer();
    }

    let animationFactory;

    const duration = this._animate ? this.duration : '0ms';

    // console.log('animate?', this._animate, 'show?', show);

    animationFactory = this.animationBuilder.build(
      useAnimation(show ? expandAnimation : collapseAnimation, { params: { time: duration, easing: this.transition}})
    );

    // if(show) {
    //   animationFactory = this.animationBuilder.build(
    //       useAnimation(expandAnimation, { params: { time: duration, easing: this.transition}})
    //     );
    // } else {
    //   animationFactory = this.animationBuilder.build(
    //       useAnimation(collapseAnimation, { params: { time: duration, easing: this.transition}})
    //     );
    // }

    this.player = animationFactory.create(this.host);
    this.player.onStart(() => {this.collapseChange.emit(show ? 'opening' : 'collapsing'); } );
    this.player.onDone(() => {this.collapseChange.emit(show ? 'open' : 'collapsed'); } );
  }
}
