import {Directive, HostListener, Input} from '@angular/core';

import {SidebarService} from './sidebar.service';

/**
 * Allows the sidebar to be minimized  via click.
 */
@Directive({
  selector: '[cSidebarMinimize]',
  exportAs: 'cSidebarMinimize'
})
export class SidebarMinimizeDirective {

  @Input('cSidebarMinimize') id: string;

  constructor(
    private sidebarService: SidebarService
  ) { }

  @HostListener('click', ['$event'])
  toggleMinimize($event: any) {
    $event.preventDefault();
    this.sidebarService.toggle({ minimize: 'toggle', id: this.id });
  }
}
