import {
  Component,
  ElementRef,
  EventEmitter,
  HostBinding, HostListener,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  Renderer2,
  SecurityContext,
  SimpleChanges
} from '@angular/core';
import {DomSanitizer} from '@angular/platform-browser';
import {useAnimation, transition, trigger, AnimationEvent, style, state, animate} from '@angular/animations';
import classNames from 'classnames';

import { showAnimation, hideAnimation } from './alert.animations';

type AnimateType = ( 'hide' | 'show' );

@Component({
  selector: 'c-alert',
  templateUrl: './alert.component.html',
  styleUrls: ['./alert.component.scss'],
  animations: [
    trigger('fadeInOut', [
      state('show', style({opacity: 1})),
      state('hide', style({opacity: 0, height: 0, padding: 0, border: 0, margin: 0 })),
      transition('show => hide', [
        animate('.3s ease-out', style({opacity: 0})),
        animate('.3s ease-out', style({height: 0, padding: 0, border: 0, margin: 0}))
      ]),
      transition('hide => show', [
        animate('.3s ease-in', style({height: '*', padding: '*', border: '*', margin: '*'})),
        animate('.3s ease-in', style({opacity: '*'}))
      ])
    ])

    // trigger('fadeInOut', [
    //   transition('* => show', [
    //     useAnimation(showAnimation, {
    //       params: { time: '600ms', easing: 'ease-in' }
    //     })
    //   ]),
    //   transition('show => hide', [
    //     useAnimation(hideAnimation, {
    //       params: { time: '600ms', easing: 'ease-out' }
    //     })
    //   ]),
    // ]),
  ],
})
export class AlertComponent implements OnInit, OnChanges, OnDestroy {

  safeIcon;
  timerId: any;
  animate: AnimateType;
  hide: boolean;
  private clockId: any;
  private _clock: number;

  get clock() {
    const time = this._clock;
    this.timer.emit(time);
    return time;
  }

  @Input() color = 'info';
  @Input() closeButton = false;
  @Input() show = true;
  @Input() fade = false;
  @Input() iconHtml = '<span>&times;</span>';
  @Input() timeout = 0;

  @Output() dismissed: EventEmitter<AlertComponent> = new EventEmitter();
  @Output() timer: EventEmitter<number> = new EventEmitter();

  // @HostListener('@fadeInOut.start', ['$event'] ) onAnimationStart($event) {
  //   this.onAnimationEvent($event)
  // }
  // @HostListener('@fadeInOut.done', ['$event']) onAnimationDone($event) {
  //   this.onAnimationEvent($event)
  // }

  @HostBinding('@.disabled')
  get animationDisabled() {
    return !this.fade;
  }

  @HostBinding('@fadeInOut')
  get animateType(): AnimateType {
    // return this.animate;
    return this.show ? 'show' : 'hide';
  }

  @HostBinding('class')
  get getClasses() {
    // return this.alertClasses;
    return this.hostClasses;
  }

  // get alertClasses() {
  get hostClasses() {

    const classes = classNames(
      {
        alert: true,
        'alert-dismissible': !!this.closeButton,
        [`alert-${this.color}`]: !!this.color
      },
    );
    return classes;
  }

  constructor(
    private sanitizer: DomSanitizer,
    private hostElement: ElementRef,
    private renderer: Renderer2
  ) {
    // this.safeIcon = sanitizer.sanitize(SecurityContext.HTML, this.iconHtml);
  }

  ngOnInit() {
    this.safeIcon = this.sanitizer.sanitize(SecurityContext.HTML, this.iconHtml);
    if (this.show && this.timeout) {
      this.setTimer();
    }
  }

  ngOnDestroy(): void {
    this.clearTimer();
    this.clearClock();
  }

  public ngOnChanges(changes: SimpleChanges): void {
    if (changes.color) {
      this.renderer.removeClass(this.hostElement.nativeElement, `alert-${changes.color.previousValue}`);
    }
    if (changes.timeout) {
      this.setTimer();
    }
  }

  setTimer() {
    this.clearTimer();
    this.timerId = this.timeout > 0 ? setTimeout(() => this.onClose(), this.timeout) : null;
    this.setClock();
  }

  clearTimer() {
    clearTimeout(this.timerId);
    this.timerId = null;
  }

  onClose() {
    this.clearTimer();
    if (this.show) {
      this.show = false;
      this.hide = true;
      this.dismissed.emit(this);
    }
  }

  setClock() {
    this.clearClock();
    this._clock = 0;
    this.clockId = setInterval(() => {
      this._clock += 1;
    }, 1000);
    setTimeout(() => {
      this.clearClock();
    }, this.timeout);
  }

  clearClock() {
    clearInterval(this.clockId);
    this.clockId = null;
  }

  onAnimationEvent( event: AnimationEvent ) {
    console.warn(event);
    if (event.phaseName === 'done' && event.toState === 'hide') {
      // this.hide = false;
    }
    if (event.phaseName === 'start' && event.toState === 'show') {
      // this.hide = true;
    }

    // openClose is trigger name in this example
    // console.warn(`Animation Trigger: ${event.triggerName}`);
    //
    // // phaseName is start or done
    // console.warn(`Phase: ${event.phaseName}`);
    //
    // // in our example, totalTime is 1000 or 1 second
    // console.warn(`Total time: ${event.totalTime}`);
    //
    // // in our example, fromState is either open or closed
    // console.warn(`From: ${event.fromState}`);
    //
    // // in our example, toState either open or closed
    // console.warn(`To: ${event.toState}`);
    //
    // // the HTML element itself, the button in this case
    // console.warn(`Element: ${event.element}`);
  }
}
