import {Component, ElementRef, HostBinding, Input, OnDestroy, OnInit, Renderer2} from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import {Observable, Subscription} from 'rxjs';
import { filter } from 'rxjs/operators';
import classNames from 'classnames';

import { SidebarNavHelper } from './sidebar-nav.service';
import {INavData} from './sidebar-nav';

@Component({
  selector: 'c-sidebar-nav-dropdown',
  template: `
    <a class="c-sidebar-nav-dropdown-toggle"
       cSidebarNavDropdownToggle
       [cHtmlAttr]="item.attributes">
      <i *ngIf="helper.hasIcon(item)" [ngClass]="item | cSidebarNavIcon"></i>
      <ng-container>{{ item.name }}</ng-container>
      <span *ngIf="helper.hasBadge(item)" [ngClass]="item | cSidebarNavBadge">{{ item.badge.text }}</span>
    </a>
    <c-sidebar-nav
      [dropdownItems]="true"
      [navItems]="navItems">
    </c-sidebar-nav>
  `,
  styleUrls: ['./sidebar-nav-dropdown.component.scss'],
  providers: [ SidebarNavHelper ]
})
export class SidebarNavDropdownComponent implements OnInit, OnDestroy {
  @Input() item: any;
  @Input() dropdownMode: 'closeInactive' | 'noAction' | 'openActive' = 'closeInactive';
  @Input() show: boolean;
  @Input() class;
  @Input() ngClass;

  @HostBinding('class')
  get getClasses() {
    return this.hostClasses;
  }

  // todo: dropdownMode

  navigationEndObservable: Observable<NavigationEnd>;
  navSubscription: Subscription;

  private open: boolean;
  public navItems: INavData[] = [];

  constructor(
    private router: Router,
    public helper: SidebarNavHelper
  ) {
    this.navigationEndObservable = router.events.pipe(
      filter(event => event instanceof NavigationEnd)
    ) as Observable<NavigationEnd>;
  }

  ngOnInit(): void {

    this.navItems = [...this.item.children];

    this.navSubscription = this.navigationEndObservable.subscribe(event => {
      const itemArray = this.item.url.split('/');
      const urlArray = event.url.split('/');
      const samePath = itemArray.every((value, index) => value === urlArray[index]);
      this.openDropdown(samePath);
    });
  }

  openDropdown(open: boolean) {
    this.open = open;
  }

  toggleDropdown() {
    this.open = !this.open;
  }

  ngOnDestroy(): void {
    this.navSubscription.unsubscribe();
  }

  get hostClasses() {

    const classes = classNames(
      this.ngClass,
      this.class,
      {
        'c-sidebar-nav-dropdown': true,
        'c-show': this.open,
      }
    );
    return classes;
  }
}
