import {Injectable} from '@angular/core';
import { Router, ActivatedRoute, NavigationEnd } from '@angular/router';
import { BehaviorSubject, Observable } from 'rxjs';
import { filter } from 'rxjs/operators';
import {IBreadcrumbItems} from './breadcrumb';

@Injectable({
  providedIn: 'root'
})
export class BreadcrumbService {

  public outlet = 'primary';

  breadcrumbs$: Observable<IBreadcrumbItems[]>;
  private breadcrumbsBehaviorSubject: BehaviorSubject<IBreadcrumbItems[]>;

  constructor(
    private router: Router,
    private route: ActivatedRoute
  ) {
    this.breadcrumbsBehaviorSubject = new BehaviorSubject<any[]>(new Array<IBreadcrumbItems>());

    this.breadcrumbs$ = this.breadcrumbsBehaviorSubject.asObservable();

    this.router.events.pipe(filter(event => event instanceof NavigationEnd)).subscribe((event) => {
      const breadcrumbs = [];
      let currentRoute = this.route.root;
      let url = '';
      do {
        const childrenRoutes = currentRoute.children;
        currentRoute = null;
        // eslint-disable-next-line no-shadow
        childrenRoutes.forEach(route => {
          // console.log('breadcrumb event', event, 'route', route);
          if (route.outlet === this.outlet) {
            const routeSnapshot = route.snapshot;
            url += '/' + routeSnapshot.url.map(segment => segment.path).join('/');
            breadcrumbs.push({
              label: route.snapshot.data.title || '',
              url,
              queryParams: routeSnapshot.queryParams
            });
            currentRoute = route;
          }
        });
      } while (currentRoute);

      this.breadcrumbsBehaviorSubject.next(Object.assign([], breadcrumbs));

      // console.log('breadcrumbs', breadcrumbs);
      return breadcrumbs;
    });
  }
}
