import {Component, ElementRef, HostBinding, Inject, Input, OnChanges, OnDestroy, OnInit, Renderer2, SimpleChanges} from '@angular/core';
import {DOCUMENT} from '@angular/common';
import {BreakpointObserver} from '@angular/cdk/layout';
import {Subscription} from 'rxjs';
import classNames from 'classnames';

import {SidebarService} from './sidebar.service';
// import {OutClickService} from "../shared";

@Component({
  selector: 'c-sidebar',
  exportAs: 'cSidebar',
  template: `<ng-content></ng-content>`
})
export class SidebarComponent implements OnInit, OnDestroy, OnChanges {
  @Input() fixed = true;
  @Input() unfoldable = false;
  @Input() overlaid = false;
  @Input() minimize = false;
  @Input() breakpoint: false | '' | 'sm' | 'md' | 'lg' | 'xl' = 'lg';
  @Input() mobile: false | '' | 'sm' | 'md' | 'lg' | 'xl' = 'lg';
  @Input() show: boolean;
  @Input() size: 'sm' | '' | 'lg' | 'xl' = '';
  @Input() aside: boolean;
  @Input() hideOnMobileClick = true;
  @Input() colorScheme = 'dark';
  @Input() id: string;

  @HostBinding('class')
  get getClasses() {
    return this.hostClasses;
  }

  private _isOnMobile: boolean;
  private _isOnDesktop: boolean;
  private stateToggleSubscription: Subscription;
  private layoutChangesSubscription: Subscription;
  // private outClickSubscription: Subscription;
  private backdrop: HTMLElement;

  // state
  openMobile;
  openDesktop;
  minimized = this.minimize;

  get getDesktopBreakpoint() {
    return getComputedStyle(this.hostElement.nativeElement).getPropertyValue(`--breakpoint-${this.breakpoint}`);
  }

  get hasBreakpoint() {
    // console.log(`sidebar-${this.id}`, ' hasBreakpoint', !!this.breakpoint);
    return !!this.breakpoint;
  }

  get getMobileBreakpoint() {
    return getComputedStyle(this.hostElement.nativeElement).getPropertyValue(`--breakpoint-${this.mobile}`);
  }

  set isOnMobile( value) {
    // this._isOnMobile = value || getComputedStyle(this.hostElement.nativeElement).getPropertyValue('--is-mobile') === 'true';
    this._isOnMobile = value;
  }
  get isOnMobile() {
    // const isMobile = getComputedStyle(this.hostElement.nativeElement).getPropertyValue('--is-mobile');
    const isMobile = Boolean(this._isOnMobile);
    // console.log(`sidebar-${this.id}`, ' isOnMobile', Boolean(isMobile));
    return isMobile;
  }

  set isOnDesktop(value) {
      this._isOnDesktop = this.hasBreakpoint ? value : false;
  }
  get isOnDesktop() {
    // console.log(`sidebar-${this.id}`, ' isOnDesktop', this._isOnDesktop);
    return this._isOnDesktop;
  }

  get isOpen() {
    let opened;
    if (this.isOnDesktop) {
      // opened = this.responsive ? true : this.openMobile;
      opened = this.openDesktop;
    } else {
      opened = (this.openMobile === true);
    }
    // console.log(`sidebar-${this.id}`, ' isOpen:', opened, 'desktop', this.openDesktop, 'mobile:', this.openMobile);
    return opened;
  }

  get hostClasses() {

    const classes = classNames(
      {
        'c-sidebar': true,
        'c-sidebar-fixed': this.fixed && !this.overlaid,
        'c-sidebar-right': this.aside,
        'c-sidebar-minimized': this.minimize && !this.unfoldable,
        'c-sidebar-unfoldable': this.minimize && this.unfoldable,
        'c-sidebar-overlaid': this.overlaid,
        'c-sidebar-show': this.openMobile === true,
        [`c-sidebar-${this.breakpoint}-show`]: (!!this.breakpoint) && this.openDesktop,
        [`c-sidebar-${this.colorScheme}`]: !!this.colorScheme,
        [`c-sidebar-${this.size}`]: !!this.size,
      }
    );
    // console.log(`sidebar-${this.id}`, ' classes', classes );
    return classes;
  }

  constructor(
    @Inject(DOCUMENT) private document: any,
    private renderer: Renderer2,
    private hostElement: ElementRef,
    private breakpointObserver: BreakpointObserver,
    private sidebarService: SidebarService,
    // private outClickService: OutClickService
  ) { }

  ngOnInit(): void {
    this.stateToggleSubscribe();
    this.layoutChangeSubscribe();
    // this.outClickSubscribe()
    this.setInitialState();
    // console.log(`sidebar-${this.id}`, ' ngOnInit', this.show, this.openDesktop, this.openMobile, 'onBreakpoint', this.isOnDesktop);
  }

  layoutChangeSubscribe(subscribe: boolean = true) {
    // console.log(`sidebar-${this.id}`, 'getDesktopBreakpoint', this.getDesktopBreakpoint)
    // console.log(`sidebar-${this.id}`, 'getMobileBreakpoint', this.getMobileBreakpoint)
    const onDesktop = `(min-width: ${this.getDesktopBreakpoint})`;
    const onMobile = this.breakpoint === false ? `(max-width: ${this.getMobileBreakpoint})` : `(max-width: ${this.getDesktopBreakpoint})`;

    if (subscribe) {
      const layoutChanges = this.breakpointObserver.observe([
        onDesktop,
        onMobile,
      ]);

      this.layoutChangesSubscription =  layoutChanges.subscribe(result => {

        this.isOnMobile = result.breakpoints[onMobile];
        this.isOnDesktop = result.breakpoints[onDesktop];
        const openMobile = this.isOnMobile ? false : this.isOpen ? this.isOpen : this.show;
        this.sidebarService.toggle({open: openMobile, sidebar: this});
        // console.log(`sidebar-${this.id}`, ' layoutChanges', result, 'onMobile', this.isOnMobile, 'onDesktop', this.isOnDesktop);
      });
    } else {
      this.layoutChangesSubscription.unsubscribe();
    }
  }

  ngOnDestroy(): void {
    // this.outClickSubscribe(false)
    this.stateToggleSubscribe(false);
    this.layoutChangeSubscribe(false);
    // console.log(`sidebar-${this.id}`, ' ngOnDestroy');
  }

  setInitialState() {
    this.openMobile = this.isOnDesktop ? false : this.isOnMobile ? false : this.show;
    this.openDesktop = this.hasBreakpoint;
    // console.log(`sidebar-${this.id}`, ' setState', 'minimized:', this.minimize, 'isOpen', this.isOpen, 'this.openMobile', this.openMobile
    // , 'this.openDesktop', this.openDesktop);
    this.sidebarService.toggle({minimize: this.minimize, open: this.isOpen, sidebar: this});
  }

  public getState() {
    return {opened: this.isOpen, minimized: this.minimized, id: this.id};
  }

  open(state: any): void {
    const toggle = (state.open === 'toggle');
    if (this.isOnDesktop) {
      this.openDesktop = toggle ? !this.openDesktop : state.open;
      this.openMobile = false;
    } else {
      this.openMobile = toggle ? !this.openMobile : state.open;
      if (this.openMobile && this.isOnMobile) {
        this.setBackdrop();
      } else {
        this.clearBackdrop();
      }
    }
    // eslint-disable-next-line max-len
    // console.log('backdrop?', `sidebar-${this.id}`, ' open()', 'openMobile', this.openMobile, 'openDesktop', this.openDesktop, 'state.open', state.open);
  }

  private stateToggleSubscribe(subscribe: boolean = true) {
    if (subscribe) {
      this.stateToggleSubscription = this.sidebarService.sidebarState$.subscribe((state) => {
        // console.log(`sidebar-${this.id}`, 'subscription state', state);
        if (this === state.sidebar || this.id === state.id) {
          if ('minimize' in state) {
            this.minimizeSidebar(state);
          }
          if ('open' in state) {
            this.open(state);
          }
        }
      });
    } else {
      this.stateToggleSubscription.unsubscribe();
    }
  }

  public hideMobile(e) {
    // console.log(`sidebar-${this.id}`, ' hideMobile', e.target, e);
    if (this.isOpen) {
      // if (!e.target.closest('[cSidebarToggle]') && !e.target.closest('[cSidebarNavDropdownToggle]')) {
      if (!e.target.closest('[cSidebarNavDropdownToggle]')) {
          this.sidebarService.toggle({open: false, sidebar: this});
      }
    }
  }

  setBackdrop() {
    const backdrop = this.document.getElementsByClassName('c-sidebar-backdrop');
    // console.log(`sidebar-${this.id}`, ' setBackdrop', backdrop);
    if (backdrop.length === 0) {
      this.backdrop = this.renderer.createElement('div');
      this.renderer.addClass(this.backdrop, 'c-sidebar-backdrop');
      this.renderer.appendChild(this.document.body, this.backdrop);
      this.renderer.listen(this.backdrop, 'click', (e) => {
        // console.log(`sidebar-${this.id}`, ' backdrop click', e);
        this.hideMobile(e);
      });
    }
    if (this.backdrop && this.isOnMobile && this.isOpen) {
      this.renderer.addClass(this.backdrop, 'c-show');
      this.renderer.removeClass(this.backdrop, 'd-none');
    } else {
      this.renderer.addClass(this.backdrop, 'd-none');
      this.renderer.removeClass(this.backdrop, 'c-show');
    }
  }

  clearBackdrop() {
    // console.log(`sidebar-${this.id}`, ' clearBackdrop', this.backdrop);
    if (this.backdrop) {
      // this.renderer.listen(this.document, 'click', (e) => {} );
      this.renderer.listen(this.backdrop, 'click', (e) => {} );
      this.renderer.removeChild(this.document.body, this.backdrop);
    }
  }

  minimizeSidebar(state) {
    const toggle = (state.minimize === 'toggle');
    this.minimize = toggle ? !this.minimize : !!state.minimize;
  }

  ngOnChanges(changes: SimpleChanges): void {
    // console.log(`sidebar-${this.id}`, ' ngOnChanges', changes)
    if (changes.breakpoint) {
      const {previousValue, currentValue} = {...changes.breakpoint};
      if (typeof currentValue === 'string') {
        this.renderer.addClass(this.hostElement.nativeElement, `c-sidebar-${currentValue}-show`);
      }
      this.renderer.removeClass(this.hostElement.nativeElement, `c-sidebar-${previousValue}-show`);
    }
    if (changes.size) {
      const {previousValue, currentValue} = {...changes.size};
      if (typeof currentValue === 'string') {
        this.renderer.addClass(this.hostElement.nativeElement, `c-sidebar-${currentValue}`);
      }
      this.renderer.removeClass(this.hostElement.nativeElement, `c-sidebar-${previousValue}`);
    }
    if (changes.colorScheme) {
      const {previousValue, currentValue} = {...changes.colorScheme};
      if (typeof currentValue === 'string') {
        this.renderer.addClass(this.hostElement.nativeElement, `c-sidebar-${currentValue}`);
      }
      this.renderer.removeClass(this.hostElement.nativeElement, `c-sidebar-${previousValue}`);
    }
  }

  // private outClickSubscribe(subscribe: Boolean = true) {
  //   if (subscribe) {
  //     this.outClickSubscription = this.outClickService.outClick$.subscribe(message => {
  //       if (message.event) {
  //         this.hideMobile(message.event);
  //       }
  //     });
  //   } else {
  //     this.outClickSubscription.unsubscribe();
  //   }
  // }

}
