import { Directive, HostBinding, Input, OnInit } from '@angular/core';
import classNames from 'classnames';

@Directive({
  selector: '[cImg]'
})
export class ImgDirective implements OnInit {

  @Input() block: boolean;
  @Input() fluid: boolean;
  @Input() fluidGrow: boolean;
  @Input() shape: string | 'rounded' | 'rounded-top' | 'rounded-right' | 'rounded-bottom' | 'rounded-left' | 'rounded-circle' | 'rounded-pill' | 'rounded-0' | 'rounded-sm' | 'rounded-lg' ;
  @Input() thumbnail: boolean;
  @Input() align: '' | 'left' | 'right' | 'center' = '';
  @Input() placeholderColor = 'transparent';

  @HostBinding('style')
  get getStyles() {
    return {backgroundColor: this.placeholderColor};
  }

  @HostBinding('class')
  get getClasses() {
    return this.hostClasses;
  }

  get hostClasses() {
    const classes = classNames(
      this.alignClass,
      {
        'img-thumbnail': this.thumbnail,
        'img-fluid': this.fluid || this.fluidGrow,
        'w-100': this.fluidGrow,
        'd-block': this.block,
        [`${this.shape}`]: this.shape,
      }
    );
    return classes;
  }

  get alignClass() {
    return this.align === 'center' ? 'mx-auto' :
      this.align === 'right' ? 'float-right' :
        this.align === 'left' ? 'float-left' : '';
  }

  constructor() {}

  ngOnInit(): void {}
}
