import {Component, HostBinding, Input, OnChanges, OnDestroy, OnInit, SimpleChanges} from '@angular/core';

import {IBreadcrumbItems} from './breadcrumb';
import {Observable, Observer} from 'rxjs';

@Component({
  selector: 'c-breadcrumb',
  templateUrl: './breadcrumb.component.html',
  styleUrls: ['./breadcrumb.component.scss']
})
export class BreadcrumbComponent implements OnChanges, OnInit, OnDestroy {

  @Input() items: IBreadcrumbItems[];
  @Input() divider = false;
  @HostBinding('class.breadcrumb') breadcrumbClass = true;

  public breadcrumbs;

  constructor(
    // public service: BreadcrumbService,
  ) { }

  public ngOnInit(): void {
    // this.setup();
  }

  public ngOnChanges(changes: SimpleChanges): void {
    if (changes.items) {
      this.setup();
    }
  }

  setup() {
    // console.log('breadcrumb setup', this.items)
    if (this.items && this.items.length > 0) {
      this.breadcrumbs = new Observable<IBreadcrumbItems[]>((observer: Observer<IBreadcrumbItems[]>) => {
        if (this.items) {
          observer.next(this.items);
        }
      });
    }
  }

  ngOnDestroy(): void {
    this.breadcrumbs = null;
  }
}
