import {
  Component,
  ElementRef,
  ChangeDetectorRef,
  HostBinding,
  ViewChild,
  AfterViewInit,
  Input
} from '@angular/core';

@Component({
  selector: 'c-toggler',
  templateUrl: './toggler.component.html',
  styleUrls: ['./toggler.component.scss']
})
export class TogglerComponent implements AfterViewInit {

  @Input() inHeader: boolean;
  @Input() inNavbar: boolean;

  @HostBinding('class.d-flex') displayFlex = true;

  @ViewChild('content') content: ElementRef;

  hasContent = true;

  constructor(private changeDetector: ChangeDetectorRef) { }

  get buttonClass() {
    if (this.inHeader) {
      return 'c-header-toggler';
    } else if (this.inNavbar) {
      return 'navbar-toggler';
    }
  }

  get iconClass() {
    if (this.inHeader) {
      return 'c-header-toggler-icon';
    } else if (this.inNavbar) {
      return 'navbar-toggler-icon';
    }
  }

  ngAfterViewInit() {
    this.hasContent = this.content.nativeElement.childNodes.length as boolean;
    // console.log('this.hasContent', this.hasContent, this.content.nativeElement);
    this.changeDetector.detectChanges();
  }
}
