import {Component, ElementRef, HostBinding, Input, OnChanges, Renderer2, SimpleChanges} from '@angular/core';
import classNames from 'classnames';

@Component({
  selector: 'c-badge',
  template: '<ng-content></ng-content>',
  styleUrls: ['./badge.component.scss']
})
export class BadgeComponent implements OnChanges {

  @Input() shape: string;
  @Input() color: string;

  @HostBinding('class')
  get getClasses() {
    return this.hostClasses;
  }

  get hostClasses() {

    const classes = classNames(
      {
        badge: true,
        [`badge-${this.color}`]: !!this.color,
        [`badge-${this.shape}`]: !!this.shape
      },
    );
    return classes;
  }

  constructor(
    private hostElement: ElementRef,
    private renderer: Renderer2,
  ) { }

  public ngOnChanges(changes: SimpleChanges): void {
    if (changes.color) {
      const classOff = `badge-${changes.color.previousValue}`;
      this.renderer.removeClass(this.hostElement.nativeElement, classOff);
    }
    if (changes.shape) {
      const classOff = `badge-${changes.shape.previousValue}`;
      this.renderer.removeClass(this.hostElement.nativeElement, classOff);
    }
  }
}
