import { Component, HostBinding, Input, OnInit } from '@angular/core';
import classNames from 'classnames';

@Component({
  selector: 'c-spinkit',
  templateUrl: './spinkit.component.html',
  styleUrls: ['./spinkit.component.scss']
})
export class SpinkitComponent implements OnInit {

  @Input() center = true;
  // eslint-disable-next-line max-len
  @Input() name: 'plane' | 'chase' | 'bounce' | 'wave' | 'pulse' | 'flow' | 'swing' | 'circle' | 'circle-fade' | 'grid' | 'fold' | 'wander' = 'plane';

  @HostBinding('class')
  get getClasses() {
    return this.hostClasses;
  }

  get hostClasses() {
    const classes = classNames(
      {
        [`sk-${this.name}`]: true,
        'sk-center' : this.center,
      },
    );
    return classes;
  }

  constructor() { }

  ngOnInit(): void {}

}
