import {Injectable} from '@angular/core';

export interface DropdownStateInterface {
  align: string;
  caret: string;
  direction: string;
  show: boolean;
}

@Injectable()
export class DropdownState {

  private state: DropdownStateInterface = {
    align: '',
    caret: 'caret',
    direction: 'down',
    show: false,
  };

  constructor() {
  }

  getState() {
    // console.log('DropdownState getState', this.state)
    return Object.assign(Object.create(null), this.state);
  }

  setState(state) {
    this.state = Object.assign(this.state, state);
    // console.log('DropdownState setState', state, this.state)
    return this.getState();
  }
}
