import {Component, ElementRef, HostBinding, HostListener, OnInit} from '@angular/core';
// import {OutClickService} from '../shared/out-click.service';
import {SidebarService} from './sidebar.service';

@Component({
  selector: 'c-sidebar-backdrop',
  templateUrl: './sidebar-backdrop.component.html',
  styleUrls: ['./sidebar-backdrop.component.scss']
})
export class SidebarBackdropComponent implements OnInit {

  @HostBinding('class.c-sidebar-backdrop') _sidebarBackdrop = true;

  @HostListener('click', ['$event'])
  public onClick($event: MouseEvent): void {
    const targetElement = $event.target as HTMLElement;
    // $event.preventDefault();
    // Check if the click was outside the element
    if (targetElement && !this.elementRef.nativeElement.contains(targetElement)) {
      this.sidebarService.toggle({open: false});
    }
  }

  constructor(
    private elementRef: ElementRef,
    private sidebarService: SidebarService

  ) { }

  ngOnInit() {
  }

}
