import {
  Component,
  OnDestroy,
  ContentChild,
  ElementRef,
  TemplateRef,
  // ChangeDetectionStrategy,
  ChangeDetectorRef,
  Input,
  HostListener, OnInit, Renderer2, Inject,
} from '@angular/core';
import {DOCUMENT} from '@angular/common';

@Component({
  selector: 'c-modal',
  templateUrl: './modal.component.html',
  styleUrls: ['./modal.component.scss']
})
export class ModalComponent implements OnDestroy, OnInit {
  @ContentChild('modalHeader', {static: true}) header: TemplateRef<any>;
  @ContentChild('modalBody', {static: true}) body: TemplateRef<any>;
  @ContentChild('modalFooter', {static: true}) footer: TemplateRef<any>;
  @Input() closeOnBackdrop = true;
  @Input() size: 'sm' | '' | 'lg' | 'xl' = '';
  @Input() color = '';

  modalClasses = {
    modal: true,
    fade: true
  };
  dialogClasses = {
    'modal-dialog': true
  };
  backdropClasses = {
    'modal-backdrop': true,
    fade: true,
    show: true
  };
  visible = false;
  visibleAnimate = false;

  constructor(
    @Inject(DOCUMENT) private document: any,
    private renderer: Renderer2,
    private elementRef: ElementRef,
    private changeDetectorRef: ChangeDetectorRef
  ) { }

  ngOnInit() {
    this.setClasses();
  }
  ngOnDestroy() {
    this.hide();
  }

  setClasses() {
    this.dialogClasses[`modal-${this.size}`] = !!this.size;
    this.dialogClasses[`modal-${this.color}`] = !!this.color;
  }

  show(): void {
    this.renderer.addClass(this.document.body, 'modal-open');

    this.visible = true;
    this.modalClasses[`d-block`] = true;
    setTimeout(() => {
      this.visibleAnimate = true;
    });
  }

  hide(): void {
    this.renderer.removeClass(this.document.body, 'modal-open');

    this.visibleAnimate = false;
    setTimeout(() => {
      this.visible = false;
      this.modalClasses[`d-block`] = false;
      this.changeDetectorRef.markForCheck();
    }, 200);
  }

  @HostListener('click', ['$event'])
  onContainerClicked(event: MouseEvent): void {
    if ((event.target as HTMLElement).classList.contains('modal') && this.isTopmost() && this.closeOnBackdrop) {
      this.hide();
    }
  }

  @HostListener('document:keydown', ['$event'])
  onKeyDownHandler(event: KeyboardEvent) {
    // If ESC key and TOP MOST modal, close it.
    if (event.key === 'Escape' && this.isTopmost()) {
      this.hide();
    }
  }

  /**
   * Returns true if this modal is the top most modal.
   */
  isTopmost(): boolean {
    const isTopmost = !this.elementRef.nativeElement.querySelector(':scope c-modal > .modal');
    return isTopmost;
  }
}
