import {Component, HostBinding, Input, OnInit} from '@angular/core';
import classNames from 'classnames';

@Component({
  selector: 'c-button-toolbar',
  template: '<ng-content></ng-content>',
  styleUrls: ['./button-toolbar.component.scss']
})
export class ButtonToolbarComponent implements OnInit {

  @Input() justify: 'between' | 'center' | 'end' | 'start' | '' = '';

  @Input() class = '';
  @Input() ngClass: any;

  private classes: any = Object.create(null);

  @HostBinding('attr.aria-label') aria = 'Button toolbar';

  @HostBinding('attr.role')
  get hostRole(): string {
    return 'toolbar';
  }

  @HostBinding('class')
  get hostClasses(): string {
    return this.getClasses();
  }

  constructor() { }

  ngOnInit() {
  }

  getClasses() {

    this.classes['btn-toolbar'] = true;
    this.classes[`justify-content-start`] = this.justify === 'start';
    this.classes[`justify-content-end`] = this.justify === 'end';
    this.classes[`justify-content-center}`] = this.justify === 'center';
    this.classes[`justify-content-between`] = this.justify === 'between';
    return classNames(this.classes, this.class, this.ngClass);
  }

}
