import {
  AfterContentChecked,
  AfterContentInit, ChangeDetectionStrategy, ChangeDetectorRef,
  Component,
  ContentChild,
  ElementRef,
  HostBinding,
  Input,
  OnInit
} from '@angular/core';
import classNames from 'classnames';
import {BreakpointObserver} from '@angular/cdk/layout';

import { CollapseDirective } from '../collapse';

@Component({
  selector: 'c-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss'],
  // changeDetection: ChangeDetectionStrategy.OnPush,
})
export class NavbarComponent implements OnInit, AfterContentInit {

  @Input() light: boolean;
  @Input() color: string;
  @Input() fixed: 'top' | 'bottom';
  @Input() sticky: boolean;
  @Input() expandable: boolean | string;

  @HostBinding('class')
  get getClasses() {
    return this.hostClasses;
  }

  get hostClasses() {
    const expandClassSuffix = this.expandable === true ? '' : `-${this.expandable}`;
    const classes = classNames(
      {
        navbar: true,
        'navbar-light': this.light,
        'navbar-dark': !this.light,
        [`navbar-expand${expandClassSuffix}`]: !!this.expandable,
        [`bg-${this.color}`]: !!this.color,
        [`fixed-${this.fixed}`]: !!this.fixed,
        'sticky-top': this.sticky,
      },
    );
    return classes;
  }

  get breakpoint() {
    if (typeof this.expandable === 'string') {
      return getComputedStyle(this.hostElement.nativeElement).getPropertyValue(`--breakpoint-${this.expandable}`);
    }
  }

  // @ContentChild(CollapseDirective, { read: ElementRef, static: false }) collapse!: CollapseDirective;
  @ContentChild(CollapseDirective) collapse!: CollapseDirective;

  collapseRef;

  constructor(
    private hostElement: ElementRef,
    private breakpointObserver: BreakpointObserver,
    // private cd: ChangeDetectorRef
  ) {}

  ngOnInit(): void { }

  ngAfterContentInit() {
    // child is set
    if (this.breakpoint) {
      const onBreakpoint = `(min-width: ${this.breakpoint})`;
      this.breakpointObserver.observe([onBreakpoint]).subscribe(result => {
        const {animate} = {...this.collapse};
        this.collapse._animate = false;
        this.collapse.show = false;
        setTimeout(() => {
          this.collapse.show = result.matches;
        }, 0);
        setTimeout(() => {
          this.collapse._animate = animate;
        });
      });
    }

  }
}
